import express from "express";
import {graphqlHTTP}  from "express-graphql";
import schema from "./schema"
const app = express()
console.log('testing');

app.get('/',(req,res)=>{
    res.json({
        msg:"Hola"})
})



app.use('/graphql', graphqlHTTP({
    schema: schema,    
    graphiql: true
  }));
app.listen(3000, ()=> console.log("Servidor en puerto 3000"))